import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router-dom'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import {
  Box,
  Button,
  Card,
  Checkbox,
  Container,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { type DateProps, defaultDate } from 'src/models/date'

import { useCheckAttendance, useGetSingleConference } from 'src/hooks/api/clubConferenceHooks'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

const Attendance: React.FC = () => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()
  const { id, confId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)
  const { userInfo } = useSafeAuthUser()

  const club = userInfo?.clubs.find(club => club.id === clubId)
  const clubDate: DateProps = { year: club?.year || defaultDate.year, semester: club?.semester || defaultDate.semester }

  const { data, isSuccess } = useGetSingleConference(clubId, clubDate, conferenceId)
  const checkAttendanceMutation = useCheckAttendance()

  const handleCheckAttendance = async (userId: number, attendanceType: boolean) => {
    await checkAttendanceMutation.mutateAsync({
      clubId,
      conferenceId,
      userId,
      attendanceType,
      clubDate
    })
  }

  const handleClickNavigateBack: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(-1)
  }

  return (
    <Box m={3}>
      <Helmet>
        <title>출석 체크</title>
      </Helmet>
      <Container>
        <Card>
          <Box display={'flex'} alignItems={'center'} p={1}>
            <Button variant="text" onClick={handleClickNavigateBack} color="primary">
              <ArrowBackIcon />
            </Button>
            <Typography variant="caption" ml={2}>
              출석체크는 자동으로 저장됩니다.
            </Typography>
          </Box>

          <Card>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>출석 여부</TableCell>
                    <TableCell>이름</TableCell>
                    {matches && <TableCell>학번</TableCell>}
                    {matches && <TableCell>학과</TableCell>}
                    {matches && <TableCell>비고</TableCell>}
                  </TableRow>
                </TableHead>
                <TableBody>
                  {isSuccess &&
                    data.member.map(member => {
                      return (
                        <TableRow hover key={member.id} data-userid={member.id}>
                          <TableCell>
                            <Checkbox
                              defaultChecked={member.attendanceType}
                              onChange={e => handleCheckAttendance(member.id, (e.target as HTMLInputElement).checked)}
                            />
                          </TableCell>
                          <TableCell>
                            <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                              {member.name}
                              {!matches && (
                                <Typography variant="subtitle1" gutterBottom noWrap>
                                  {member.studentId}
                                </Typography>
                              )}
                            </Typography>
                          </TableCell>

                          {matches && (
                            <TableCell>
                              <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                                {member.studentId}
                              </Typography>
                            </TableCell>
                          )}
                          {matches && (
                            <TableCell>
                              <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                                {member.department}
                              </Typography>
                            </TableCell>
                          )}
                          {matches && (
                            <TableCell>
                              <Typography
                                variant="body1"
                                fontWeight="bold"
                                color="text.primary"
                                noWrap
                                sx={{ maxWidth: '300px' }}
                              >
                                {member.note}
                              </Typography>
                            </TableCell>
                          )}
                        </TableRow>
                      )
                    })}
                </TableBody>
              </Table>
            </TableContainer>
          </Card>
        </Card>
      </Container>
    </Box>
  )
}

export default Attendance
